.container {
  background-color: #f1f1f1;
  box-sizing: border-box;
  padding: 1rem;
  overflow-y: scroll;

  @media (min-width: 900px) {
    background-color: transparent;
    border: none;
    height: 100%;
    padding: 0 1rem 0 1rem;
    width: auto;

    &::-webkit-scrollbar {
      background-color: #d9d9d9;
      border-radius: 5px;
      width: 3px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: #afaeae;
      border-radius: 5px;
    }
  }
}

.loadingMessages {
  align-items: center;
  display: flex;
  font-size: 1.6rem;
  height: 100%;
  justify-content: center;
}

.containerMessage {
  display: flex;
  flex-direction: column;
}

.dayMonth {
  align-self: center;
  background-color: #d4d4d4;
  border-radius: 5px;
  box-sizing: border-box;
  margin: 1rem 0;
  padding: 0.5rem 0.7rem;

  p {
    margin: 0;
  }
}

.singleMessage {
  display: flex;
  flex-direction: column;
  gap: 0.6rem;
  margin: 0.5rem 0;
  max-width: 220px;
  padding: 0.5rem 0.7rem 0.3rem;
  width: fit-content;

  p {
    margin: 0;
  }

  @media (min-width: 900px) {
    max-width: 320px;
    padding: 0.4rem 0.5rem;
  }
}

.myMessage {
  background-color: #fff;
  border-radius: 10px 10px 10px 0;
  color: rgba(0, 0, 0, 0.7);

  @media (min-width: 900px) {
    background-color: rgb(241, 241, 241);
  }
}

.userMessage {
  align-self: flex-end;
  background-color: #595454;
  border-radius: 10px 10px 0 10px;
  color: #fff;

  p {
    &:last-of-type {
      align-self: flex-end;
      color: #fff;
    }
  }
}

.messageHour {
  color: rgba(0, 0, 0, 0.7);
  font-size: 0.8rem;
  margin: 0;
}

.focus {
  background-color: transparent;
  border: none;
  height: 0;
  width: 0;
}
